import React, { useRef, useState } from 'react';
import Alert from '../../alert';
import AlertIcon from '../../alert-icon';
import AlertLabel from '../../alert-label';
import Col from '../../col';
import Grid from '../../grid';
import Icon from '../../icon';
import Row from '../../row';
import Typography from '../../typography';
import AlertLabelTitle from '../../alert-label-title';
import ButtonContainer from '../../button-container';
import ButtonGroup from '../../button-group';
import ButtonText from '../../button-text';
import ButtonIcon from '../../button-icon';

const AlertExample = globalState => {
  const [showAlert, setShowAlert] = useState(false);
  const [count, setCount] = useState(1);
  const [alertTitle, setAlertTitle] = useState();
  const [alertContent, setAlertContent] = useState();
  const buttonRef = useRef();

  return (
    <Grid>
      <div>
        {
          <Alert
            alertType="success"
            id="alert-success-test"
            position="absolute"
            show={showAlert}
            sidenav="open"
          >
            <Grid>
              <Row>
                <Col aria-live="assertive">
                  <AlertIcon><Icon name="success" resolution="low" /></AlertIcon>
                  <AlertLabel>
                    <AlertLabelTitle>{alertTitle}</AlertLabelTitle>
                    <span>{alertContent}</span>
                  </AlertLabel>
                </Col>
                <Col sm={1}>
                  <ButtonContainer>
                    <ButtonGroup direction="right">
                      <ButtonText
                        onClick={() => {
                          setShowAlert(false);
                          setAlertTitle('');
                          setAlertContent('');
                          buttonRef.current.focus();
                          setCount(count + 1);
                        }}
                        colorScheme="tertiary"
                        tabIndex={showAlert ? 0 : -1}
                      >
                        Dismiss
                      </ButtonText>
                    </ButtonGroup>
                  </ButtonContainer>
                </Col>
              </Row>
            </Grid>
          </Alert>
        }
      </div>
      <Row>
        <Col>
          <Typography tag="p">
            Use optional props in the table below to manage timeout and onBeforeClose states
          </Typography>
          <ButtonText
            colorScheme="secondary"
            onClick={() => {
              setShowAlert(true);
              setTimeout(() => {
                setAlertTitle(`Success: Information Updated ${count}.`);
                setAlertContent(`This is a alert notification ${count}.`);
              });
            }}
            ref={buttonRef}
          >
            Open Global Alert
          </ButtonText>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Timed (Default)</Typography>
          <Alert id="alert-default-1" position="relative">
            <Grid>
              <Row>
                <Col>
                  <AlertIcon>
                    <Icon name="info" resolution="low" />
                  </AlertIcon>
                  <AlertLabel>This is a alert notification.</AlertLabel>
                </Col>
              </Row>
            </Grid>
          </Alert>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Alert (success)</Typography>
          <Alert id="alert-default-2" position="relative" alertType="success">
            <Grid>
              <Row>
                <Col>
                  <AlertIcon>
                    <Icon name="success" resolution="low" />
                  </AlertIcon>
                  <AlertLabel>This is a alert notification.</AlertLabel>
                </Col>
              </Row>
            </Grid>
          </Alert>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Alert (warning)</Typography>
          <Alert id="alert-default-3" position="relative" alertType="warning">
            <Grid>
              <Row>
                <Col>
                  <AlertIcon>
                    <Icon name="info" resolution="low" />
                  </AlertIcon>
                  <AlertLabel>This is a alert notification.</AlertLabel>
                </Col>
              </Row>
            </Grid>
          </Alert>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Alert (error)</Typography>
          <Alert id="alert-default-4" position="relative" alertType="error">
            <Grid>
              <Row>
                <Col>
                  <AlertIcon>
                    <Icon name="error" resolution="low" />
                  </AlertIcon>
                  <AlertLabel>This is a alert notification.</AlertLabel>
                </Col>
              </Row>
            </Grid>
          </Alert>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Alert Call to Action (success)</Typography>
          <Alert id="alert-success" position="relative" alertType="success">
            <Grid>
              <Row>
                <Col>
                  <AlertIcon><Icon name="success" resolution="low" /></AlertIcon>
                  <AlertLabel>
                    <AlertLabelTitle>Success: Information Updated.</AlertLabelTitle>
                    This is a alert notification.
                  </AlertLabel>
                </Col>
                <Col sm={1}>
                  <ButtonContainer>
                    <ButtonGroup direction="right">
                      <ButtonText colorScheme="tertiary">Dismiss</ButtonText>
                    </ButtonGroup>
                  </ButtonContainer>
                </Col>
              </Row>
            </Grid>
          </Alert>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Alert Call to Action (close button)</Typography>
          <Alert id="alert-error" position="relative" alertType="error">
            <Grid>
              <Row>
                <Col>
                  <AlertIcon><Icon name="error" resolution="low" /></AlertIcon>
                  <AlertLabel>
                    <AlertLabelTitle>Error: Could not process your request.</AlertLabelTitle>
                    There has been an error processing your request. Please try again later.
                  </AlertLabel>
                </Col>
                <Col sm={1}>
                  <ButtonGroup direction="right">
                    <ButtonIcon ariaLabel="search" iconType="light-tiny">
                      <Icon name="close" resolution="tiny" />
                    </ButtonIcon>
                  </ButtonGroup>
                </Col>
              </Row>
            </Grid>
          </Alert>
        </Col>
      </Row>
    </Grid>
  );
};

export default AlertExample;
